import leapYear from './calcyear';
import myMask from './mask';

const biorhythm = () => {


    let canvas = document.getElementById("myCanvas"),
        cx = canvas.getContext("2d");

    let canvasGadget = document.getElementById("myCanvasGadget"),
        cxGadget = canvasGadget.getContext("2d");


    let dateBirthday = {
        year: 0,
        month: 0,
        date: 0
    };

    let currentDate = {
        year: 0,
        month: 0,
        date: 0
    };

    //Рабочий с querySelector
    const button = document.querySelector('.btn');

    button.addEventListener('click', () => {
        let corentData = document.querySelector('.date-corrent-value').value,
            myDate = document.querySelector('.date-birthday').value;

        let t = myDate.split('.');
        dateBirthday.year = t[2];
        dateBirthday.month = parseInt(t[1] - 1);
        dateBirthday.date = parseInt(t[0]);
        t = corentData.split('.');
        currentDate.year = t[2];
        currentDate.month = parseInt(t[1] - 1);
        currentDate.date = parseInt(t[0]);
        checkSizeSreen();
    });


    //заполняем input датами
    const fillingInput = (dateBirthday) => {

        currentDate.year = new Date().getFullYear(),
        currentDate.month = new Date().getMonth(),
        currentDate.date = new Date().getDate();


        let month = currentDate.month < 9 ? '0' + (+currentDate.month + 1) : currentDate.month + 1;
        let date = currentDate.date < 10 ? '0' + currentDate.date : currentDate.date;

        let myMonth = dateBirthday.month < 9 ? '0' + (+dateBirthday.month + 1) : dateBirthday.month + 1;
        let myDate = dateBirthday.date < 10 ? '0' + dateBirthday.date : dateBirthday.date;


        document.querySelector('.date-corrent-value').value = date + '.' + month + '.' + currentDate.year;
        document.querySelector('.date-birthday').value = myDate + '.' + myMonth + '.' + dateBirthday.year;
    };

    function showBiorhythms(dateBirthday, currentDate, color_background) {

        canvas.style.display = "flex";
        canvasGadget.style.display = "none";
        cx.clearRect(0, 0, 1000, 480); //очистить от предыдущих линий

        cx.fillStyle = color_background; // Установка цвета заливки и цвет шрифта
        cx.fillRect(0, 0, 1000, 480); // Установка размера границ заливки 

        // cx.strokeStyle = 'blue';
        cx.lineWidth = 1;
        cx.shadowBlur = 10;
        cx.shadowOffsetY = 20;
        // cx.shadowColor = "black";

        let pi = Math.PI,
            physicalCycle = 23,
            emotionalCycle = 28,
            intellectualCycle = 33,
            offsetY = 200,
            offsetX = 50,
            scaleX = 30,
            // offsetL = 870; //длина линии ОХ
            offsetL = 870, //длина линии ОХ
            controlDay = 30;

        offsetL = (controlDay - 1) * scaleX;

        let dateEnd = new Date(currentDate.year, currentDate.month, currentDate.date);
        let dateBegin = new Date(dateBirthday.year, dateBirthday.month, dateBirthday.date);

        let daysLived = (dateEnd.getTime() - dateBegin.getTime()) / (1000 * 3600 * 24);
        console.log('Вы прожили ', daysLived, " дней.");

        cx.beginPath(); //начинает контур
        cx.moveTo(offsetX, offsetY); // cx.moveTo(x, y); 
        cx.lineTo(offsetX + offsetL, offsetY);
        cx.moveTo(offsetX, offsetY); // cx.moveTo(x, y); 
        cx.strokeStyle = 'black'; // strokeStyle св-во устанавливает/возвращает цвет | градиент | шаблон;


        /* Начало - рисуем штрих и цифры на оси*/
        let dateGraph = currentDate.date; //берем текущую дату
        cx.font = "8pt Arial";
        for (let index = 0; index < controlDay; index++) {
            cx.strokeStyle = 'black'; // strokeStyle св-во устанавливает/возвращает цвет | градиент | шаблон;
            let i = index * scaleX;
            cx.moveTo(i + offsetX, offsetY - 10); // cx.moveTo(x, y); 
            cx.lineTo(i + offsetX, offsetY + 10);
            cx.moveTo(i + offsetX, offsetY - 10); // cx.moveTo(x, y); 

            if (currentDate.month == 0 ||
                currentDate.month == 2 ||
                currentDate.month == 4 ||
                currentDate.month == 6 ||
                currentDate.month == 7 ||
                currentDate.month == 9 ||
                currentDate.month == 11) {
                (dateGraph > 31) ? dateGraph = 1: dateGraph;
            } else if (currentDate.month == 1) {
                let leapyear = leapYear(currentDate.year);
                let febr = leapyear == 1 ? 29 : 28;
                (dateGraph > febr) ? dateGraph = 1: dateGraph;
            } else {
                (dateGraph > 30) ? dateGraph = 1: dateGraph;
            }

            cx.fillStyle = 'green'; //Установка цвета заливки и цвет шрифта
            cx.fillText(dateGraph++, i - 4 + offsetX, offsetY + 20);
        }
        /* Конец - рисуем штрих и цифры на оси*/

        cx.stroke(); //русуем контур

        cx.moveTo(offsetX, offsetY); // рисуем ось

        /* Начало - рисуем биоритмы */
        if (isNaN(stringDate) != false && isNaN(stringDate) != false) {
            function draw(daysLived, dateCycle, clr) {
                cx.beginPath(); //начинает контур
                cx.strokeStyle = clr;
                for (let index = 0; index < controlDay; index++) {
                    let b = Math.sin(2 * pi * (-daysLived) / dateCycle) * 150 + offsetY;
                    daysLived--;
                    let i = index * scaleX;
                    cx.lineTo(i + offsetX, b);
                    cx.moveTo(i + offsetX, b);
                }

                cx.stroke(); //русуем контур
            }

            draw(daysLived, physicalCycle, 'blue');
            draw(daysLived, emotionalCycle, 'red');
            draw(daysLived, intellectualCycle, 'green');
        }
        /* Конец - рисуем биоритмы */


        /* Начало - рисуем легенду*/
        cx.font = "15pt Arial";
        cx.fillStyle = 'black'; //Установка цвета заливки и цвет шрифта
        function outputLegend(title, deltaY, color) {
            cx.beginPath(); //начинает контур
            cx.moveTo(10, offsetY + deltaY - 8); // cx.moveTo(x, y); 
            cx.lineTo(30, offsetY + deltaY - 8);
            cx.strokeStyle = color; //цвет линии
            cx.stroke(); //русуем контур

            cx.fillText(title, offsetX, offsetY + deltaY);
        }

        outputLegend("Физический биоритм", 200, 'blue');
        outputLegend("Эмоциональный биоритм", 230, 'red');
        outputLegend("Интеллектуальный биоритм", 260, 'green');
        /* конец - рисуем легенду*/
    }

    function showBiorhythmsGadget(dateBirthday, currentDate, color_background) {

        let main = document.getElementsByClassName('main'),
            date = document.getElementsByClassName('date'),
            dateCorrentValue = document.getElementsByClassName('date-corrent-value'),
            dateBirth = document.getElementsByClassName('date-birthday'),
            btn = document.getElementsByClassName('btn'),
            h = document.getElementsByTagName('h2'),
            text = document.querySelectorAll('.text');


        for (let i = 0; i < main.length; i++) {
            main[i].style.display = "block";
            main[i].style.float = "center";
            main[i].style.width = "480px";

        }

        for (let i = 0; i < date.length; i++) {
            date[i].style.width = "450px";
            date[i].style.fontSize = "26px";
            // date[i].style.marginTop  = "3%";
            date[i].style.height = "23px";
        }

        // dateCorrentValue[0].style.width  = "200px";
        dateBirth[0].style.width = "440px";
        dateBirth[0].style.fontSize = "26px";
        dateBirth[0].style.textAlign = "center";
        dateBirth[0].style.marginTop = "3%";
        dateBirth[0].style.height = "23px";

        dateCorrentValue[0].style.width = "440px";
        dateCorrentValue[0].style.fontSize = "26px";
        dateCorrentValue[0].style.textAlign = "center";
        dateCorrentValue[0].style.marginTop = "3%";
        dateCorrentValue[0].style.height = "23px";

        btn[0].style.display = 'inline';
        btn[0].style.fontSize = "26px";
        btn[0].style.height = "48px";
        btn[0].style.width = "470px";
        btn[0].style.marginTop = "3%";

        // main[i].style.width  = "210px";
        // main[i].style.height  = "20px";

        h[0].style.width = '460px';

        text.forEach(function (item, i, text) {
            item.style.font = "14pt Arial";
            item.style.width = '470px';
        });



        canvas.style.display = "none";
        canvasGadget.style.display = "flex";

        cxGadget.clearRect(0, 0, 1000, 500); //очистить от предыдущих линий

        cxGadget.fillStyle = color_background; // Установка цвета заливки и цвет шрифта
        cxGadget.fillRect(0, 0, 1000, 500); // Установка размера границ заливки 
        cxGadget.lineWidth = 2;
        cxGadget.shadowBlur = 10;
        cxGadget.shadowOffsetY = 20;

        let pi = Math.PI,
            physicalCycle = 23,
            emotionalCycle = 28,
            intellectualCycle = 33,
            offsetY = 200,
            offsetX = 22,
            scaleX = 30,
            offsetL = 870, //длина линии ОХ
            controlDay = 15;

        offsetL = (controlDay - 1) * scaleX;

        let dateEnd = new Date(currentDate.year, currentDate.month, currentDate.date);
        let dateBegin = new Date(dateBirthday.year, dateBirthday.month, dateBirthday.date);

        let daysLived = (dateEnd.getTime() - dateBegin.getTime()) / (1000 * 3600 * 24);
        console.log('Вы прожили ', daysLived, " дней.");

        cxGadget.beginPath(); //начинает контур
        cxGadget.moveTo(offsetX, offsetY); // cx.moveTo(x, y); 
        cxGadget.lineTo(offsetX + offsetL, offsetY);
        cxGadget.moveTo(offsetX, offsetY); // cx.moveTo(x, y); 
        cxGadget.strokeStyle = 'black'; // strokeStyle св-во устанавливает/возвращает цвет | градиент | шаблон;


        /* Начало - рисуем штрих и цифры на оси*/
        let dateGraph = currentDate.date; //берем текущую дату
        cxGadget.font = "14pt Arial";
        for (let index = 0; index < controlDay; index++) {
            cxGadget.strokeStyle = 'black'; // strokeStyle св-во устанавливает/возвращает цвет | градиент | шаблон;
            let i = index * scaleX;
            cxGadget.moveTo(i + offsetX, offsetY - 10); // cx.moveTo(x, y); 
            cxGadget.lineTo(i + offsetX, offsetY + 10);
            cxGadget.moveTo(i + offsetX, offsetY - 10); // cx.moveTo(x, y); 
            if (currentDate.month == 1 ||
                currentDate.month == 3 ||
                currentDate.month == 5 ||
                currentDate.month == 7 ||
                currentDate.month == 8 ||
                currentDate.month == 10 ||
                currentDate.month == 12) {
                (dateGraph > 31) ? dateGraph = 1: dateGraph;
            } else if (currentDate.month == 2) {
                let leapyear = leapYear(currentDate.year);
                let febr = leapyear == 1 ? 29 : 28;
                (dateGraph > febr) ? dateGraph = 1: dateGraph;
            } else {
                (dateGraph > 30) ? dateGraph = 1: dateGraph;
            }

            cxGadget.fillStyle = 'green'; //Установка цвета заливки и цвет шрифта
            cxGadget.fillText(dateGraph++, i - 10 + offsetX, offsetY + 30);
        }
        /* Конец - рисуем штрих и цифры на оси*/

        cxGadget.stroke(); //русуем контур

        cxGadget.moveTo(offsetX, offsetY); // рисуем ось

        /* Начало - рисуем биоритмы */
        if (isNaN(stringDate) != false && isNaN(stringDate) != false) {
            function draw(daysLived, dateCycle, clr) {
                cxGadget.beginPath(); //начинает контур
                cxGadget.strokeStyle = clr;
                for (let index = 0; index < controlDay; index++) {
                    let b = Math.sin(2 * pi * (-daysLived) / dateCycle) * 150 + offsetY;
                    daysLived--;
                    let i = index * scaleX;
                    cxGadget.lineTo(i + offsetX, b);
                    cxGadget.moveTo(i + offsetX, b);
                }

                cxGadget.stroke(); //русуем контур
            }

            draw(daysLived, physicalCycle, 'blue');
            draw(daysLived, emotionalCycle, 'red');
            draw(daysLived, intellectualCycle, 'green');
        }
        /* Конец - рисуем биоритмы */

        /* Начало - рисуем легенду*/
        cxGadget.font = "16pt Arial";
        cxGadget.fillStyle = 'black'; //Установка цвета заливки и цвет шрифта
        function outputLegend(title, deltaY, color) {
            cxGadget.beginPath(); //начинает контур
            cxGadget.moveTo(10, offsetY + deltaY - 8); // cx.moveTo(x, y); 
            cxGadget.lineTo(30, offsetY + deltaY - 8);
            cxGadget.strokeStyle = color; //цвет линии
            cxGadget.stroke(); //русуем контур

            cxGadget.fillText(title, offsetX + 25, offsetY + deltaY);
        }

        outputLegend("Физический биоритм", 200, 'blue');
        outputLegend("Эмоциональный биоритм", 230, 'red');
        outputLegend("Интеллектуальный биоритм", 260, 'green');
        /* конец - рисуем легенду*/
    }



    myMask("date-corrent-mask"); //текущая дата обрабатывается маской src\js\main.js
    myMask("date-birthday-mask"); //дата ДР обрабатывается маской src\js\main.js


    /* Начало - Модальное окно, разбор даты*/
    let stringDate = prompt('Введите дату в формате ДД.ММ.ГГГГ', '');

    if (isNaN(stringDate) == false) {
        stringDate = "01.1.1900";
    }

    let t = stringDate.split('.');
    dateBirthday.year = t[2];
    dateBirthday.month = parseInt(t[1] - 1);
    dateBirthday.date = parseInt(t[0]);
    /* Конец - Модальное окно, разбор даты*/

    fillingInput(dateBirthday);

    // showBiorhythms(dateBirthday, currentDate); //вызов старой функции - нарисовать биоритмы

    //проверка на размер экрана (для ПК, для ТЛФ)
    function checkSizeSreen() {
        if (window.outerWidth > 767) {
            cx.clearRect(0, 0, 1000, 480);
            // cx.fillStyle  = 'red'; // цвет заливки
            showBiorhythms(dateBirthday, currentDate, '#a9bee3'); //вызов функции - нарисовать биоритмы
            // showBiorhythms_gadget(dateBirthday, currentDate, '#a9bee3'); //вызов функции - нарисовать биоритмы

        } else {
            cx.clearRect(0, 0, 1000, 480);
            // cx.fillStyle  = 'blue'; // цвет заливки
            // showBiorhythms(dateBirthday, currentDate, 'blue'); //вызов функции - нарисовать биоритмы
            showBiorhythmsGadget(dateBirthday, currentDate, '#e4e7ae'); //вызов функции - нарисовать биоритмы
        }
    }

    checkSizeSreen();


};

export default biorhythm;