export default function leapYear(currentYear) {

    let j = 0;

    let element = currentYear % 4;
    if (!element) {
        element = currentYear % 100;
        if (element) {
            j = 1;
        } else {
            element = currentYear % 400;
            if (!element) {
                j = 1;
            }
        }
    }
    return j;
}